/* jshint ignore:start */

define('crud-baseline/config/environment', ['ember'], function(Ember) {
  var prefix = 'crud-baseline';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("crud-baseline/tests/test-helper");
} else {
  require("crud-baseline/app")["default"].create({"name":"crud-baseline","version":"0.0.0+16ca2927"});
}

/* jshint ignore:end */
